"use client";

import { useState, useMemo } from "react";
import Image from "next/image";
import styles from '../page.module.css';

export default function MediaGallery({ initialMedias }) {
  const [sortBy, setSortBy] = useState("popularity");

  // Logique de tri automatique dès que sortBy change
  const sortedMedias = useMemo(() => {
    const list = [...initialMedias];
    return list.sort((a, b) => {
      if (sortBy === "popularity") return b.likes - a.likes;
      if (sortBy === "date") return new Date(b.date) - new Date(a.date);
      if (sortBy === "title") return a.title.localeCompare(b.title);
      return 0;
    });
  }, [sortBy, initialMedias]);

  return (
    <>
      <div className={styles.sortContainer}>
        <label htmlFor="sort-select">Trier par</label>
        <select 
          id="sort-select"
          value={sortBy}
          onChange={(e) => setSortBy(e.target.value)}
        >
          <option value="popularity">Popularité</option>
          <option value="date">Date</option>
          <option value="title">Titre</option>
        </select>
      </div>

      <div className={styles.gallery}>
        {sortedMedias.map((media) => (
          <div key={media.id} className={styles.mediaCard}>
            {media.image ? (
              <Image
                src={`/assets/${media.image}`}
                alt={media.title}
                width={350}
                height={350}
                style={{ objectFit: 'cover', borderRadius: '5px' }}
              />
            ) : (
              <video width={350} height={350} controls style={{ borderRadius: '5px', objectFit: 'cover' }}>
                <source src={`/assets/${media.video}`} type="video/mp4" />
              </video>
            )}
            <div className={styles.mediaInfo}>
              <p>{media.title}</p>
              <p>{media.likes} ♥</p>
            </div>
          </div>
        ))}
      </div>
    </>
  );
}